.page {
  font-family: "Poppins", sans-serif;

  &__body {
    margin: 0;
    height: 100vh;
    width: 100vw;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(-100%);

    &:target {
    transform: translateX(0);

    transition: transform .3s;
    }
  }
}
